body {
    font-family: Arial, sans-serif;
    background-color: #fff;
    margin: 0;
    padding: 0;
}

.main-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 20px 120px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}

.skeleton {
    background-color: #f0f0f0;
    border-radius: 4px;
    margin-bottom: 10px;
}

.skeleton-logo {
    width: 80px;
    height: 65px;
}

.skeleton-nav-item {
    width: 100px;
    height: 30px;
    margin-left: 20px;
}

.skeleton-button {
    width: 120px;
    height: 40px;
    margin-left: 20px;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 80px 120px;
}

.skeleton-title {
    width: 300px;
    height: 40px;
    margin: 20px 0;
}

.skeleton-row {
    display: flex;
    align-items: center;
}

.skeleton-word {
    width: 100px;
    height: 40px;
    margin: 0 10px;
}

.skeleton-subtitle {
    width: 400px;
    height: 20px;
    margin: 10px 0;
}

.skeleton-button {
    width: 280px;
    height: 50px;
    margin: 20px 0;
}

.skeleton-divider {
    width: 100%;
    height: 2px;
    margin: 20px 0;
}

.skeleton-link {
    width: 200px;
    height: 20px;
    margin: 10px 0;
}

.skeleton-text {
    width: 300px;
    height: 20px;
    margin: 10px 0;
}

